<style>

#app {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
}

.index-box header {
    background: #ff2772;
    color: #fff;
    line-height: 0.6rem;
}

.index-box header a {
    font-size: 0.27rem;
}

.index-box .sc {
    width: 100%;
    text-align: center;
    font-size: 0.27rem;
    color: #fff;
    float: left;
}

.app-fade-enter-active {
    transition: all .3s ease;
}

.app-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}

.app-fade-enter,
.app-fade-leave-active {
    padding-right: 500px;
    padding-left: 500px;
    opacity: 0;
}
.search-fade-enter-active {
  transition: all .3s ease;
}
.search-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.search-fade-enter, .search-fade-leave-active {
  transform: translateX(-10px);
  opacity: 0;
}
</style>

<template>
<div class="index-box">
    <header class="clearfix">
        <div class="sc" @click="searchShow=true">优兑商城</div>
    </header>
    <banner v-bind:banner="{styleObj:{height:'3rem'},itemData:getCarousel,dataType:1,filterType:5}" :bannerConfig="bannerConfig"></banner>
    <navlist :navData="getChannelInfo"></navlist>
    <!-- <kyyq></kyyq> -->
    <transition name="search-fade">
        <search v-show="searchShow" :searchType="'mall'" v-on:hide="hideSearch"></search>
    </transition>
</div>
</template>

<script>

import {
    mapGetters
}
from 'vuex'
import banner from './../common/banner.vue'
import navlist from './../common/nav-list.vue'
import search from './../common/search.vue'
// import kyyq from './kyyq.vue'

const components = {
    banner,navlist, search
}

export default {
    data() {
        return {
            searchShow : false,
            bannerConfig : {
                direction: 'horizontal',
                autoplay: 2000,
                spend: 2000,
                loop: true,
                observer: true, //修改swiper自己或子元素时，自动初始化swiper
                observeParents: true,
                //修改swiper的父元素时，自动初始化swiper
                // 如果需要分页器
                pagination: '.swiper-pagination'
            }
        }
    },
    computed: mapGetters({
        getCarousel: 'getCarousel',
        getChannelInfo:'getChannelInfo'
    }),
    created() {
        this.$store.dispatch('getmallIndexData',{type:1});
    },
    methods : {
        hideSearch : function(){
            this.searchShow = false;
        }
    },
    name: 'mall-index',
    components: components
}

</script>



